import React from "react";
import logo from "../assets/img/logo.png";
import lapbook from "../assets/img/lapbook.png";

class SignIn extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: ''
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        alert('A name was submitted: ' + this.state.username);
        alert('A password was submitted: ' + this.state.password);

        //Call function and vwerify credentials and proceed
        event.preventDefault();
    }

    handleChange(event) {
        this.setState({ [event.target.name]: event.target.value});
    }


  render() {
    return (
            <section>
                <div className="row">
                    <div className="col-sm-12 col-md-12 col-lg-4 col-xl-4 half">
                        <div className="user_sign_left_blk">
                            <div className="logo_blk">
                                <img src={logo} alt="logo" />
                            </div>
                            <form className="form-signin" onSubmit={this.handleSubmit}>
                                <div className="form_heading_blk">
                                    <h2 className="form_heading">Sign In</h2>
                                </div>
                                <div className="">
                                    <div className="input-holder">
                                        <label className="floating-label">Email Address</label>
                                        <input type="email" name="username" className="form-control signin-input" value={this.state.username} onChange={this.handleChange}/>
                                    </div>
                                    <div className="input-holder mt15">
                                        <label className="floating-label">Password</label>
                                        <input type="password" name="password" value={this.state.password} onChange={this.handleChange} className="form-control signin-input" securetextentry="true"/>
                                    </div>
                                    <div className="chk_blk">
                                        <label className="chk_label">
                                            Keep me logged in
                                            <input type="checkbox" />
                                            <span className="checkmark"></span>
                                        </label>
                                    </div>
                                    <div className="btn_blk">
                                        <button type="submit" className="btn btn-primary btn_signin">SIGN IN</button>
                                        <a href="" className="forget_psd">
                                        Forgot Password
                                        </a>
                                    </div>
                                </div>
                                <div className="signup_now">
                                    <h6>
                                    Don't have an account?
                                    <a href="" style={{ color: "#573594", fontWeight: 700 }}>{" "}Sign up now</a>
                                    </h6>
                                    <div className="partition">Or</div>
                                    <div className="social_icons_blk">
                                        <h6>Continue with social media</h6>
                                        <div className="social_icons">
                                            <a href="">
                                                <i className="fab fa-facebook-f fb"></i>
                                            </a>
                                        </div>
                                        <div className="social_icons">
                                            <a href="">
                                                <i className="fab fa-twitter"></i>
                                            </a>
                                        </div>
                                        <div className="social_icons">
                                            <a href="http://localhost:8001/google/auth">
                                                <i className="fab fa-google-plus-g gplus"></i>
                                            </a>
                                        </div>
                                        <div className="social_icons">
                                            <a href="">
                                                <i className="fab fa-linkedin-in linkdin"></i>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="col-sm-12 col-md-12 col-lg-8 col-xl-8 bg_sign_rht half">
                        <div className="user_sign_rht_blk">
                            <div className="sign_rht_content">
                                <h1 className="sign_rht_caption">
                                We're about to change your visual discovery process
                                </h1>
                                <h6 className="sign_rht_descrip">
                                It is a long established fact that a reader will be
                                distracted by the readable content of a page when looking at
                                its layout.
                                </h6>
                            </div>
                            <div className="sign_rht_img_blk">
                                <img src={lapbook} className="sign_rht_img" alt="lapbook" />
                            </div>
                        </div>
                    </div>
                </div>
            </section>
    );
  }
}

export default SignIn;
