import React from "react";
import { OTSession, OTPublisher, OTStreams, OTSubscriber, createSession } from 'opentok-react';
import Axioslib from "../lib/Axioslib";
import FeatherIcon from 'feather-icons-react';


class Videoconference extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            apikey: "46438532",
            sessionId: "",
            token: "",
            streams: [],
            activeStreams: {},
            clickedAudioStreams:{},
            clickedVideoStreams:{}
        };

        this.subscribers = {};
        
        this.publisherProperties = {
            // audioFallbackEnabled: false,
            showControls: false,
            resolution: '640x480'
        };

        this.subscriberProperties = {
            audioFallbackEnabled: false,
            showControls: false,
            preferredFrameRate: 7,
            subscribeToAudio:true, 
            subscribeToVideo:true,
            resolution: '640x480'
        };

        this.publisherEventHandlers = {
            streamCreated: event => {
                console.log('Publisher stream created!');
            },
            streamDestroyed: event => {
                console.log('Publisher stream destroyed!');
            }
        };

        this.subscriberEventHandlers = {
            streamCreated: event => {
                console.log('Subscriber stream created!');
            },
            streamDestroyed: event => {
                console.log('Subscriber stream destroyed!');
            },
            videoDisabled: event => {
                console.log('Subscriber video disabled!');
            },
            videoEnabled: event => {
                console.log('Subscriber video enabled!');
            },
            audioDisabled: event => {
                console.log('Subscriber audio disabled!');
            },
            audioEnabled: event => {
                console.log('Subscriber audio enabled!');
            }
        };
    }

    componentDidMount() {

        Axioslib.post("/videoconference/getsession").then(response => {
            console.log(response.data);

            if(response.data.success === true) {
                this.sessionid  =   response.data.result.sessionid;
                this.token      =   response.data.result.token;

                if(this.sessionid !== "" && this.sessionid !== "undefined" && typeof this.sessionid !== "undefined" && this.token !== "" && this.token !== "undefined" && typeof this.token !== "undefined") {
                    this.setState({sessionId: this.sessionid, token: this.token});

                    console.log(this.state);

                    this.sessionHelper = createSession({
                        apiKey: this.state.apikey,
                        sessionId: this.state.sessionId,
                        token: this.state.token,
                        onStreamsUpdated: streams => { this.setState({ streams }); }
                    });
                    this.setState({
                        sessionHelper:this.sessionHelper
                    })

                    console.log(this.sessionHelper);

                } else {
                    alert("Please reload");
                }
            }
        });
    }

    componentWillUnmount() {
        this.sessionHelper.disconnect();
    }
    renderPublisher () {
        if(this.state.sessionHelper !== undefined) return (<div><OTPublisher session={this.state.sessionHelper.session} properties={this.publisherProperties} /><FeatherIcon icon='video' size='30' className='text-primary'/><FeatherIcon icon='mic' size='30' className='text-primary'/></div> )
    }

    componentDidUpdate () {
        // console.log(this.state.streams);
        // console.log(this.state.activeStreams);

    }

    handleVideoPublish(id) {
        let clickedVideoStreams = this.state.clickedVideoStreams
        if(clickedVideoStreams[id] == undefined) clickedVideoStreams[id] = true
        else clickedVideoStreams[id] = !clickedVideoStreams[id]

        this.setState({
            clickedVideoStreams :{...clickedVideoStreams}
        })
        // console.log(id, this.state.clickedVideoStreams)

        if(this.state.clickedVideoStreams[id]) {
            if(this.subscribers[id]) this.subscribers[id].state.subscriber.subscribeToVideo(false)
        } else {
            if(this.subscribers[id]) this.subscribers[id].state.subscriber.subscribeToVideo()
        }

        let subscriber = this.subscribers[id]
        // OTSession.publish(subscriber, function(error) {
        //     if (error) {
        //       console.log(error);
        //     } else {
        //       console.log('Publishing a stream.');
        //     }
        //   });

        // let stream = this.state.streams.filter((stream) => stream.id == id)
        // stream.map(subscriber => {
        //     console.log(subscriber);
        //     // subscriber.subscribeToVideo(false);
        // })
        // this.setState({
        //     streams : [...this.state.streams]
        // })
        // console.log(this.state.streams)
    }

    handleAudioPublish(id){
        let clickedAudioStreams = this.state.clickedAudioStreams
        if(clickedAudioStreams[id] == undefined) clickedAudioStreams[id] = true
        else clickedAudioStreams[id] = !clickedAudioStreams[id]

        this.setState({
            clickedAudioStreams :{...clickedAudioStreams}
        })

        if(this.state.clickedAudioStreams[id]) {
            if(this.subscribers[id]) this.subscribers[id].state.subscriber.subscribeToAudio(false)
        } else {
            if(this.subscribers[id]) this.subscribers[id].state.subscriber.subscribeToAudio()
        }

        // let stream = this.state.streams.filter((stream) => stream.id == id)
        // stream.map(subscriber => {
        //     console.log(subscriber);
        //     // subscriber.subscribeToVideo(false);
        // })
    }

    render() {
        return (    
            <div>

                {this.renderPublisher()}

                {this.state.streams.map(stream => {
                    let streamId = stream.id;
                    let videoIconId =   "videoicon_"+streamId;
                    let audiIconId =   "audioicon_"+streamId;

                    return (
                        
                        <div key={stream.id}>
                            <OTSubscriber 
                                session={this.sessionHelper.session} 
                                stream={stream} 
                                properties={this.subscriberProperties} 
                                eventHandlers={this.subscriberEventHandlers} 
                                ref={(instance) => { this.subscribers[streamId] = instance }} 
                            />

                            <FeatherIcon icon={! this.state.clickedVideoStreams[stream.id] ? 'video' : 'video-off'} id={videoIconId} size="30" className={! this.state.clickedVideoStreams[stream.id] ? 'text-primary' : 'text-danger'} onClick={()=> this.handleVideoPublish(stream.id, videoIconId)}/>
                            <FeatherIcon icon={! this.state.clickedAudioStreams[stream.id] ? 'mic' : 'mic-off'} id={audiIconId} size="30" className={! this.state.clickedAudioStreams[stream.id] ? 'text-primary' : 'text-danger'} onClick={()=> this.handleAudioPublish(stream.id, audiIconId)}/>
                        </div>

                    );
                })}
            </div>
        );
    }
}

export default Videoconference;